import { Route, Routes } from "react-router-dom"
import { Home } from "./modules/Home";
import './App.css';

function App() {
  return (
    <>
     
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/home" element={<Home />} />
        </Routes>
      
    </>
  );
}

export default App;
